
























































































import {Component, Vue, Watch} from 'vue-property-decorator';
import {Radio, RadioGroup, Button, Toast, Divider, Overlay, Loading} from 'vant';
import ScanSign from '@/componets/scan_sign.vue';
import {notes, NoteType} from '@/util/note';
import {CashboxJs} from '@/util/cashboxjs';
import 'vant/lib/radio-group/style';
import 'vant/lib/radio/style';
import 'vant/lib/button/style';
import 'vant/lib/toast/style';
import 'vant/lib/divider/style';
import 'vant/lib/overlay/style';
import 'vant/lib/loading/style';
import {AuthBusinessType, AuthBusinessStatus, Business} from "../../model/business_model";
import {ToastUtil} from "../../util/toast";
import {DiamondUtil} from "../../util/diamond_util";
import {checkAddressInfo} from "../../util/utils";
import GlobalConfig from "../../config/global_config";

Vue.use(RadioGroup).use(Radio).use(Button).use(Divider).use(Button).use(Overlay).use(Loading);
@Component({
    components: {ScanSign},
})
export default class Input extends Vue {
    private inputName = '';
    private inputAddress = ''; // replace address
    private isLoadingAccount = false;
    private signText = 'empty data';
    private showQr = false;                            //是否显示二维码
    private showScanQrBtn = false;                     //是否扫码获取授权按钮
    private isShowNextStepBtn = false;                 //是否显示下一步 上链按钮
    private isForbidChangeUsername = false;            // 是否禁止操作用户名
    private isForbidChangeAddress = false;             // 是否禁止操作地址
    private isForbidChangeAuthCount = false;           // 是否禁止操作授权次数
    private countTime = 1000;                           // 授权访问次数
    private AddBusiness = AuthBusinessType.Auth;        // 操作类型：增加
    private DeleteBusiness = AuthBusinessType.Cancel;   // 操作类型：删除
    private radioAuthBtnValue = this.AddBusiness;      // 默认操作类型是 增加
    private bodyHeight = 0;                             // 动态调整页面高度。处理如：键盘弹出等现象问题
    private waitSignInfo = '';                          // 拼接 待签名信息
    private businessModel = new Business();
    private signedValue = '';                           // 签名后的信息
    private authAddress = "";
    private lastFn?: any;                               // 执行的任务
    private addressRightIcon = GlobalConfig.ICON_ARROW_DOWN;
    private isShowAddressPopup = false;
    private addressList = Array();
    private SeparateSymbol = ";";

    public created(): void {
        this.$store.state.title = this.$t('businessAuthorize');
        {   // 注册 调用原生方法 返回调用方法
            (window as any).nativeQrScanToJsResult = this.nativeQrScanToJsResult;
        }
    }

    public async mounted() {
        this.bodyHeight = document.documentElement.clientHeight * 93 / 100;  // 除去7%的title高度
        // let nativeAddress = "5GrwvaEF5zXb26Fz9rcQpDWS57CtERHpNehXCPcNoHGKutQY";               // todo remove test account
        let businessList: Business[] = await DiamondUtil.getInstance().loadBusinessList(this.$store.state.nativeChainAddress);
        businessList.forEach((business: Business) => {
            this.addressList.push(business.name + this.SeparateSymbol + business.address);
        });
    }

    @Watch('radioAuthBtnValue', {immediate: true, deep: true})
    public onChangeAuthType(val: AuthBusinessType): void {
        if (val === this.AddBusiness) {
            this.businessModel.authType = AuthBusinessType.Auth;
            this.addressRightIcon = GlobalConfig.ICON_SCAN;
        } else if (val === this.DeleteBusiness) {
            this.businessModel.authType = AuthBusinessType.Cancel;
            this.addressRightIcon = GlobalConfig.ICON_ARROW_DOWN;
        } else {
            ToastUtil.show("操作类型选择有误，请重新检查后选择，改为默认增加业务员");
            this.businessModel.authType = AuthBusinessType.Auth;
        }
        this.changeQrContent();
    }

    @Watch('inputAddress', {immediate: true, deep: true})
    public changeInputAddress() {
        this.changeQrContent();
    }

    @Watch('inputName', {immediate: true, deep: true})
    public changeInputName() {
        this.changeQrContent();
    }

    private changeQrContent() {
        if (this.lastFn !== null) {
            clearTimeout(this.lastFn);
        }
        this.lastFn = setTimeout(() => {
            this.lastFn = null as any;
            this.assembleQrInfo();
        }, GlobalConfig.FORM_QR_INTERVAL);
    }

    public assembleQrInfo() {
        if (this.inputName && this.inputAddress && this.countTime !== 0) {
            if (!checkAddressInfo(this.inputAddress)) {
                ToastUtil.fail("输入的地址格式有问题，请重新检查");
                console.log("this.inputAddress.toString().length===>" + this.inputAddress.toString().length);
                return "";
            }
            //二维码规则：    1、时间戳有效期暂定+半小时       2、操作类型dtt=02（签名操作业务员）
            this.isLoadingAccount = true;
            this.loadWaitSignInfo().then((res: string) => {
                    if (res && res.toString().length !== 0) {
                        this.signText = 'https://cashbox.scry.info/qr?ct=66&ot=ds&dt=0&dtt=' + GlobalConfig.QR_PARAM_BUSINESS +
                            '&v=' + res + '&tl=' + GlobalConfig.QR_USEFUL_TIME() + '&';
                        console.log("this.signText:" + this.signText);
                        this.showQr = true;
                        this.showScanQrBtn = true;
                    } else {
                        this.showQr = false;
                        ToastUtil.fail("生成授权二维码数据失败，请重新打开应用尝试");
                    }
                    this.isLoadingAccount = false;
                }
            ).catch((e: any) => {
                this.isLoadingAccount = false;
                this.showQr = false;
                console.log("e===>" + e.toString());
                ToastUtil.fail("生成授权二维码出现错误" + e.toString());
            });
        } else {
            this.isLoadingAccount = false;
            this.showQr = false;
            ToastUtil.fail("请检查数据不能为空");
        }
    }

    public async loadWaitSignInfo() {
        // let nativeAddress = "5CHvQU81NU367NohiMBxuWsfLMaNucZ4Vw3kG1g5EvhjBc9H";      //alice  //todo
        if (this.radioAuthBtnValue === this.AddBusiness) {
            this.waitSignInfo = await DiamondUtil.getInstance().assembleAddBusinessInfo(this.$store.state.nativeChainAddress, this.inputName, this.inputAddress, this.countTime);
        } else {
            this.waitSignInfo = await DiamondUtil.getInstance().assembleDeleteBusinessInfo(this.$store.state.nativeChainAddress, this.inputAddress);
        }
        return this.waitSignInfo;
    }

    //扫码的方式，输入地址信息0x666777...
    public scanOrChooseAddress() {
        if (this.radioAuthBtnValue === this.AddBusiness) {
            CashboxJs.getInstance().callNativeQrScanToJs();
        } else {
            this.isShowAddressPopup = true;
        }
    }

    public confirmPickerAddress(value: string): void {
        this.isShowAddressPopup = false;
        if (value) {
            let index = value.indexOf(this.SeparateSymbol);
            this.inputName = value.substring(0, index);
            this.inputAddress = value.substring(index + 1, value.length);
        }
    }

    //扫描授权后的二维码信息
    public scanToSignedQr(): void {
        CashboxJs.getInstance().callNativeQrScanToJs();
    }

    public nativeQrScanToJsResult(scannedValue: string) {
        let signedValue = '';
        // 根据二维码签名格式，筛选获取签名部分信息.
        if (scannedValue.indexOf(";") === -1) {     //扫描结果，是通过扫描 输入地址的情况
            this.inputAddress = scannedValue;
            ToastUtil.show('二维码信息符合地址格式，已录入地址!');
            return;
        }
        const paramsList: string[] = scannedValue.split(';');
        let paramsMap: { [key: string]: string } = {};
        for (let i = 0; i < paramsList.length; i++) {
            const index = i;
            const tagIndex = paramsList[index].indexOf('=');
            if (tagIndex !== -1) {
                paramsMap[paramsList[index].substring(0, tagIndex)] = paramsList[index].substring(tagIndex + 1);
            }
        }
        if (paramsMap.dtt && paramsMap.dtt === GlobalConfig.QR_PARAM_BUSINESS && paramsMap.v) {//授权业务员dtt为02
            signedValue = paramsMap.v;
        } else {
            ToastUtil.show('二维码格式有问题,不符合 授权业务员 签名格式');
            return;
        }
        if (paramsMap.addr) {
            this.authAddress = paramsMap.addr;
        }
        console.log("nativeQrScanToJsResult is ===>" + signedValue + ' || ' + paramsMap.dtt + "||" + paramsMap.addr);
        ToastUtil.show('解析二维码成功!');
        this.signedValue = signedValue;
        {   //更改显示效果
            this.isForbidChangeUsername = true;
            this.isForbidChangeAuthCount = true;
            this.isForbidChangeAddress = true;
            this.isShowNextStepBtn = true;
            this.showScanQrBtn = false;
        }
    }

    public clickNextStep() {
        this.businessModel.authAddress = this.authAddress;
        this.businessModel.maxCount = this.countTime;
        this.businessModel.name = this.inputName;
        this.businessModel.address = this.inputAddress;
        this.businessModel.signInfo = this.signedValue;
        this.businessModel.authState = AuthBusinessStatus.SUCCESS;
        this.$store.commit('updateNowBusiness', this.businessModel);
        {
            this.$router.push({
                name: 'business_confirm',
            });
        }
    }
}
